//import scrollTo from 'react-scroll-to-component'

class Nav extends React.Component {

	constructor(props) {
		super(props)

		this.changeSection = this.changeSection.bind(this);
	}

	componentDidMount() {
		this.scrollTo = require('react-scroll-to-component')
	}

	render () {
		return (
			<nav className="main-nav">
					{this.props.links.map((link, i) => {
						return (
							<a className="main-nav__link"
							   key={i}
							   href={link.href}
							   onClick={this.changeSection.bind(null, link.href)}
							>
								{link.name}
							</a>
						)
					})}
			</nav>
		)
	}

}

export default Nav
